// eslint-disable-next-line strict
const sliderInner = document.querySelector('.slider-inner');
const prevButton = document.querySelector('.arrow_right');
const nextButton = document.querySelector('.arrow_left');
// eslint-disable-next-line no-unused-vars
const slides = document.querySelectorAll('.slide');

// eslint-disable-next-line prefer-const, no-unused-vars
let currentPosition = 0;
let isAnimating = false;

nextButton.addEventListener('click', () => {
  if (isAnimating) {
    return;
  }

  isAnimating = true;

  sliderInner.style.transition = 'transform 0.5s ease-in-out';
  sliderInner.style.transform = `translateX(-100%)`;

  setTimeout(() => {
    const firstSlide = sliderInner.querySelector('.slide');

    sliderInner.appendChild(firstSlide); // Переміщуємо перший слайд в кінець
    sliderInner.style.transition = 'none';
    sliderInner.style.transform = 'translateX(0)';

    isAnimating = false;
  }, 500);
});

prevButton.addEventListener('click', () => {
  if (isAnimating) {
    return;
  }

  isAnimating = true;

  const lastSlide = sliderInner.querySelector('.slide:last-child');

  // eslint-disable-next-line max-len
  sliderInner.insertBefore(lastSlide, sliderInner.firstChild); // Переміщуємо останній слайд на початок
  sliderInner.style.transition = 'none';
  sliderInner.style.transform = `translateX(-100%)`;

  setTimeout(() => {
    sliderInner.style.transition = 'transform 0.5s ease-in-out';
    sliderInner.style.transform = 'translateX(0)';
    sliderInner.style.transform = 'opacity : 0';

    isAnimating = false;
  }, 10);
});

// eslint-disable-next-line no-unused-vars
const slideInterval = 3000; // Час між перемиканнями слайдів (в мілісекундах)

// Стили

document.addEventListener('DOMContentLoaded', () => {
  const styleSwitcherButton = document.getElementById('style-switcher');
  const lightThemeLink = document.getElementById('main-theme');
  const darkThemeLink = document.getElementById('blue-theme');

  // Function to set the theme
  function setTheme(theme) {
    if (theme === 'blue') {
      darkThemeLink.disabled = false;
      lightThemeLink.disabled = true;
      // eslint-disable-next-line no-undef
      localStorage.setItem('theme', 'blue'); // Save preference
    } else {
      darkThemeLink.disabled = true;
      lightThemeLink.disabled = false;
      // eslint-disable-next-line no-undef
      localStorage.setItem('theme', 'main'); // Save preference
    }
  }

  // Check for saved theme preference on load
  // eslint-disable-next-line no-undef
  const savedTheme = localStorage.getItem('theme');

  if (savedTheme) {
    setTheme(savedTheme);
  } else {
    // Default to light if no preference is saved
    setTheme('main');
  }

  // Event listener for the button
  styleSwitcherButton.addEventListener('click', () => {
    // eslint-disable-next-line max-len
    if (lightThemeLink.disabled) { // If light theme is currently disabled, switch to light
      setTheme('main');
    } else { // Otherwise, switch to dark
      setTheme('blue');
    }
  });
});
